import { define, WeElement } from 'omi'
// import logo from './logo.svg'  //vscodejshtml
import '../app-intro'
// import 'omiu/button'
import 'omiu'

const vscode = acquireVsCodeApi();
const callbacks = {};

window.addEventListener('message', event => {
  const message = event.data;
  switch (message.cmd) {
      case 'vscodeCallback':
          console.log(message.data);
          (callbacks[message.cbid] || function () { })(message.data);
          delete callbacks[message.cbid];
          break;
      default: break;
  }
});

define('my-app', class extends WeElement {
  css = require('./_index.css')

  name = 'Omi'

  userName = ''
  time = ''
  show = true

  install() {
    this.time = this.getTime()
    this.callVscode({cmd: 'getConfig', key: 'omi.user.name'}, userName => this.userName = userName)
    this.callVscode({cmd: 'getConfig', key: 'omi.start.welcome'}, show => this.show = show)
    this.userName = "aaaaaaaaaaaa"
    this.update()
  }

  clickHandler = () => {
    this.name = 'Omio'
    this.update()
  }

  clickShow = () => {
    if(this.show) {
      this.show = false
    } else {
      this.show = true
    }
    this.callVscode({cmd: 'setConfig', key: 'omi.start.welcome', value: this.show}, null)
    this.update()
  }

  getTime() {
    const hour = new Date().getHours();
    if (hour <= 8) return 'good morning';
    else if (hour < 12) return 'good morning';
    else if (hour < 14) return 'good afternoon';
    else if (hour < 18) return 'good afternoon';
    return 'good evening';
  }

  callVscode(data, cb) {
    if (typeof data === 'string') {
      data = { cmd: data };
    }
    if (cb) {
      // 5
      const cbid = Date.now() + '' + Math.round(Math.random() * 100000);
      callbacks[cbid] = cb;
      data.cbid = cbid;
    }
    vscode.postMessage(data);
  }

  render() {
    return (
      <div class="app">
        <header class="app-header">
          <img
            src="data:image/svg+xml;base64,"
            onClick={this.clickHandler}
            class="app-logo"
            alt="logo"
          />
          <h2 class="app-title">Hi {this.userName}, {this.time}Welcome to {this.name}</h2>
        </header>
        {/* <app-intro /> */}
        {/* <o-button onClick={this.clickHandler} style='width:200px;'>I am omiu button.</o-button> */}
        <table class="o-switch-dis" border="15">
          <tr>
            <th>Displays a custom welcome page at startup</th>
            <th><o-switch onChange={this.clickShow} checked={this.show} /></th>
          </tr>
        </table>
      </div>
    )
  }
})
